


































































































.active::before {
  content: '';
  width: 7rem;
  height: 7rem;
  border-radius: 100%;
  border: 6px solid var(--test-color);
  position: absolute;
  z-index: 10;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: ring 0.23s infinite;
}

@keyframes ring {
  0% {
    width: 7.5rem;
    height: 7.5rem;
    opacity: 1;
  }
  100% {
    width: 12rem;
    height: 12rem;
    opacity: 0.25;
  }
}
